import React, { useEffect, useState } from 'react'
import { setCardPayload } from './Action.js'
import Payment from 'payment'
import { connect } from 'react-redux'
import Cards from 'react-credit-cards'
import 'react-credit-cards/es/styles-compiled.css'

const Credit_Debit = (props) => {
  const { currencyCode, passengerData } = props
  const { disablePayButton } = props
  const [number, setNumber] = useState('')
  const [focus, setfocus] = useState('')
  const [name, setName] = useState('')
  const [cvv, setCvv] = useState('')
  const [issuer, setIssuer] = useState('')
  const [enteredDate, setEnteredDate] = useState('')
  const [enteredDateError, setEnteredDateError] = useState('')
  const [errorCVV, setErrorCVV] = useState('')
  const [errorName, setErrorName] = useState('')
  const [errorNumber, setErrorNumber] = useState('')
  const [firstName, setFirstName] = useState('')
  const [lastName, setLastName] = useState('')
  const [email, setEmail] = useState('')
  const [phone, setPhone] = useState('')
  const validateCardHolderName = (name) => {
    setName(name)
    setErrorName('')
  }
  const validateCardCVV = (CVV) => {
    if (Payment.fns.validateCardCVC(CVV)) {
      setCvv(CVV)
      setErrorCVV('')
    } else {
      disablePayButton(true)
      setErrorCVV('*Invalid CVV')
    }
  }
  const errorOccured = (name) => {
    setName(name)
    disablePayButton(true)
    setErrorName('*Invalid Name')
  }

  useEffect(() => {
    if (
      number &&
      name &&
      cvv &&
      enteredDate &&
      !enteredDateError &&
      !errorCVV &&
      !errorName &&
      !errorNumber
    ) {
      const dateFormat = enteredDate.split('/')
      const dateValue =
        '20'.concat(dateFormat[1]) + '-' + dateFormat[0] + '-' + '01'
      setFirstName(passengerData?.name?.first)
      setLastName(passengerData?.name?.last)
      setEmail(passengerData?.email)
      setPhone(passengerData?.phoneNumber)
      const payload = {
        number,
        cvv,
        name,
        dateValue,
        currencyCode,
        issuer,
        firstName,
        lastName,
        email,
        phone
      }
      console.log('cardDetails', payload)
      disablePayButton(false)
    } else {
      disablePayButton(true)
    }
  }, [number, name, cvv, enteredDate, issuer, phone])

  const validateNumber = (cardNum) => {
    if (Payment.fns.validateCardNumber(cardNum)) {
      setErrorNumber('')

      setNumber(cardNum.split(' ').join(''))
    } else {
      disablePayButton(true)
      setErrorNumber('*Invalid Card Number')
    }
  }

  const clearNumber = (value = '') => {
    return value.replace(/\D+/g, '')
  }

  const formatCreditCardNumber = (value) => {
    if (!value) {
      return value
    }

    const issuer = Payment.fns.cardType(value)
    const clearValue = clearNumber(value)
    let nextValue
    setIssuer(issuer)
    switch (issuer) {
      case 'amex':
        nextValue = `${clearValue.slice(0, 4)} ${clearValue.slice(
          4,
          10
        )} ${clearValue.slice(10, 15)}`
        break
      case 'dinersclub':
        nextValue = `${clearValue.slice(0, 4)} ${clearValue.slice(
          4,
          10
        )} ${clearValue.slice(10, 14)}`
        break
      default:
        nextValue = `${clearValue.slice(0, 4)} ${clearValue.slice(
          4,
          8
        )} ${clearValue.slice(8, 12)} ${clearValue.slice(12, 16)}`
        break
    }
    return nextValue.trim()
  }
  const formatccv = (value, prevValue, allValues = {}) => {
    const clearValue = clearNumber(value)
    let maxLength = 4

    if (allValues.number) {
      const issuer = Payment.fns.cardType(allValues.number)
      maxLength = issuer === 'amex' ? 4 : 3
    }

    return clearValue.slice(0, maxLength)
  }
  const formatExpirationDate = (value) => {
    const clearValue = clearNumber(value)

    if (clearValue.length >= 3) {
      return `${clearValue.slice(0, 2)}/${clearValue.slice(2, 4)}`
    }

    return clearValue
  }
  const setExpiryDate = (date) => {
    setEnteredDate(date)
    if (Payment.fns.validateCardExpiry(date)) {
      setEnteredDateError('')
    } else {
      disablePayButton(true)
      setEnteredDateError('*Invalid expiry date')
    }
  }
  const handleInputFocus = (e) => {
    setfocus(e.target.name)
  }
  const handleInputChange = ({ target }) => {
    if (target.name === 'name') {
      setName(target.value)
      if (!target.value) return setErrorName('')
      const name = target.value
      const regexName = /^[a-zA-Z]+(([a-zA-Z ])?[a-zA-Z]*)*$/
      let regexNameCheck = false
      regexNameCheck = regexName.test(name)
      name
        ? regexNameCheck
          ? validateCardHolderName(name)
          : errorOccured(name)
        : validateCardHolderName(name)
    } else if (target.name === 'number') {
      setNumber(target.value)
      if (!target.value) {
        setIssuer('')
        setErrorNumber('')
        return
      }
      target.value = formatCreditCardNumber(target.value)
      validateNumber(target.value)
    } else if (target.name === 'expiry') {
      if (!target.value) return setEnteredDateError('')
      target.value = formatExpirationDate(target.value)
      setExpiryDate(target.value)
    } else if (target.name === 'cvc') {
      setCvv(target.value)
      if (!target.value) return setErrorCVV('')
      target.value = formatccv(target.value)
      validateCardCVV(target.value)
    }
  }

  return (
    <div className="card">
      <div className="card-title">Enter your card details</div>{' '}
      <div className="container" style={{ display: 'flex' }}>
        <form style={{ marginTop: '10px', width: '60%' }}>
          <div
            style={{
              textAlign: 'left',
              position: 'relative',
              marginBottom: '20px'
            }}
          >
            <span
              style={{
                textAlign: 'left',
                right: '4%',
                top: '10px',
                position: 'absolute'
              }}
            >
              {issuer?.toUpperCase()}{' '}
            </span>
            <input
              id="card-number"
              type="tel"
              name="number"
              className="form-control"
              placeholder={'xxxx  -  xxxx  -  xxxx  -  xxxx'}
              pattern="[\d| ]{16,22}"
              required
              onChange={handleInputChange}
              onFocus={handleInputFocus}
              style={{
                color: 'darkgray',
                borderRadius: '0px',
                border: 'none',
                borderBottom: errorNumber
                  ? ' 1px solid red'
                  : '1px solid lightgrey',
                letterSpacing: '5.5px',
                fontSize: '13.5px'
              }}
            />
            <sub style={{ position: 'absolute', top: '48px', color: 'red' }}>
              {errorNumber}
            </sub>
          </div>
          <div
            className="row form-group"
            style={{
              textAlign: 'left',
              position: 'relative',
              marginBottom: '20px'
            }}
          >
            <div className="col-7">
              <input
                id="card-expiry"
                type="tel"
                name="expiry"
                className="form-control"
                placeholder={'MM/YY'}
                pattern="\d\d/\d\d"
                required
                onChange={handleInputChange}
                onFocus={handleInputFocus}
                style={{
                  borderRadius: '0px',
                  color: 'darkgray',
                  border: 'none',
                  borderBottom: enteredDateError
                    ? ' 1px solid red'
                    : '1px solid lightgrey',
                  letterSpacing: '3px',
                  fontSize: '13.5px'
                }}
              />
              <sub style={{ position: 'absolute', top: '48px', color: 'red' }}>
                {enteredDateError}
              </sub>
            </div>
            <div className="col-5">
              <input
                id="card-cvv"
                type="tel"
                name="cvc"
                className="form-control"
                placeholder="CVV"
                pattern="\d{3,4}"
                required
                onChange={handleInputChange}
                onFocus={handleInputFocus}
                style={{
                  borderRadius: '0px',
                  color: 'darkgray',
                  webkitTextSecurity: 'disc',
                  border: 'none',
                  borderBottom: errorCVV
                    ? ' 1px solid red'
                    : '1px solid lightgrey',
                  letterSpacing: '2px',
                  fontSize: '13.5px'
                }}
              />
              <sub style={{ position: 'absolute', top: '48px', color: 'red' }}>
                {errorCVV}
              </sub>
            </div>
          </div>
          <div
            className="form-group"
            style={{
              textAlign: 'left',
              position: 'relative',
              paddingBottom: '12px'
            }}
          >
            <input
              id="card-name"
              type="string"
              name="name"
              className="form-control"
              placeholder={'Name on the Card'}
              required
              onChange={handleInputChange}
              onFocus={handleInputFocus}
              style={{
                color: 'darkgray',
                borderRadius: '0px',
                border: 'none',
                borderBottom: errorName
                  ? ' 1px solid red'
                  : '1px solid lightgrey',
                letterSpacing: '1.5px',
                fontSize: '13.5px'
              }}
            />
            <sub style={{ position: 'absolute', top: '48px', color: 'red' }}>
              {errorName}
            </sub>
          </div>
        </form>
        {/* <Cards
          cvc={cvv}
          expiry={enteredDate}
          focused={focus}
          name={name}
          number={number}
        /> */}
      </div>
    </div>
  )
}

const mapStateToProps = (state) => {}
const mapDispatchToProps = (dispatch) => {
  return {
    setCardPayload: (state) => dispatch(setCardPayload(state))
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(Credit_Debit)
