import React, { useState } from 'react'
import axios from 'axios'
import { Button } from 'reactstrap'
import Channels from './PaymentChannels/PaymentChannels'
import Summary from './PaymentChannels/Summary'
import Logo from '../../../assets/img/logo/LogoLight.png'

const loadScript = (src) => {
  return new Promise((resolve) => {
    const script = document.createElement('script')
    script.src = src
    document.body.appendChild(script)
    script.onload = () => {
      resolve(true)
    }
    script.onerror = () => {
      resolve(false)
    }
  })
}
const createOrder = () => {
  return new Promise((resolve) => {
    const body = {
      amount: 100,
      currency: 'INR',
      receipt: 'Receipt no. 1',
      payment_capture: 1,
      notes: {
        notes_key_1: 'Tea, Earl Grey, Hot',
        notes_key_2: 'Tea, Earl Grey… decaf.'
      }
    }
    var config = {
      method: 'post',
      url: 'https://api.razorpay.com/v1/orders',
      headers: {
        'Content-Type': 'application/json',
        Authorization:
          'Basic cnpwX3Rlc3RfcHB1Q3cxMVVTSVFReW06YVAyOGtkMUp1VFRwcGV3Z3JYaUVlR0ox'
      },
      data: body
    }
    axios(config)
      .then((response) => {
        console.log('response', response)
        resolve(response)
      })
      .catch((error) => {
        resolve(error)
      })
  })
}
const src = 'https://checkout.razorpay.com/v1/checkout.js'
const Payments = (props) => {
  const [plan, setplan] = useState([])
  const domain = document.domain === 'lastarzi.com'

  const displayRazorpay = async (id) => {
    const res = await loadScript(src)
    if (!res) return alert('Loading Failed')
    const options = {
      key: !domain ? 'rzp_test_ppuCw11USIQQym' : 'unavailable',
      amount: '100', //min currency unit,e.g. paise for INR
      currency: 'INR',
      name: 'LastArzi',
      description: 'Pay Dev',
      image: Logo,
      order_id: id ? id : 'order_HPND4SN74ODVSp', //This is a sample Order ID. Pass the `id` obtained in the response of Step 1
      callback_url: 'https://eneqd3r9zrjok.x.pipedream.net/',
      prefill: {
        name: 'Vipin Kumar',
        email: 'vksforum@gmail.com',
        contact: '9458706129'
      },
      notes: {
        address: 'LastArzi Corporate Office'
      },
      theme: {
        color: '#3399cc'
      }
    }

    const paymentObject = new window.Razorpay(options)
    paymentObject.open()
    // document.getElementById('rzp-button1').onclick = function (e) {
    //   rzp1.open()
    //   e.preventDefault()
    // }
  }

  const createOrderRazorpay = async () => {
    const res = await createOrder(src)
    displayRazorpay(res?.id)
    console.log('response', res)
  }
  return (
    <>
      <div className="list-view product-checkout" style={{ display: 'flex' }}>
        <Channels heading="Payment Options" />
        <Summary plan={plan} setplan={setplan} />{' '}
      </div>

      <div className="d-flex justify-content-end flex-wrap mt-2">
        <Button.Ripple
          className="mr-1"
          color="primary"
          onClick={(e) => {
            createOrderRazorpay()
            // displayRazorpay()
          }}
        >
          Pay
        </Button.Ripple>
      </div>
    </>
  )
}

export default Payments
