import React, { useState } from 'react'
import Select from 'react-select'
import { Label } from 'reactstrap'
const walletOptions = [
  {
    value: 'Gpay',
    label: 'Gpay',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'Phonepe',
    label: 'Phonepe',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'Paytm',
    label: 'Paytm',
    color: '#00B8D9',
    isFixed: true
  }
]
const UPI = (props) => {
  const [wallet, setwallet] = useState('')
  return (
    <div className="card">
      <div className="card-title">Select a Wallet </div>{' '}
      <div className="container" style={{ marginTop: '10px' }}>
        <Select
          id="walletOptions"
          className="React"
          classNamePrefix="select"
          name="wallet"
          options={walletOptions}
          isClearable={true}
          placeholder="Select..."
          onChange={(e) => {
            e?.value && setwallet(e.value)
          }}
        />
      </div>
    </div>
  )
}
export default UPI
