import React, { useState } from 'react'
import { TabContent, TabPane, Nav, NavItem, NavLink } from 'reactstrap'
import classnames from 'classnames'
import Card from './Card'
import UPI from './UPI'
import Wallet from './Wallet'
import NetBanking from './NetBanking'
import SavedCard from './SavedCard'

const TabsVerticalLeft = () => {
  const [active, setActive] = useState('1')
  const [disablePayButton, setDisablePayButton] = useState(true)

  const toggle = (tab) => {
    if (active !== tab) {
      setActive(tab)
    }
  }
  return (
    <div style={{ width: '75%' }}>
      <div className="nav-vertical">
        <br />
        <Nav tabs className="nav-left">
          <NavItem>
            <NavLink
              className={classnames({
                active: active === '0'
              })}
              onClick={() => {
                toggle('0')
              }}
            >
              Saved Cards
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={classnames({
                active: active === '1'
              })}
              onClick={() => {
                toggle('1')
              }}
            >
              Cards
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={classnames({
                active: active === '2'
              })}
              onClick={() => {
                toggle('2')
              }}
            >
              UPI
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={classnames({
                active: active === '3'
              })}
              onClick={() => {
                toggle('3')
              }}
            >
              Wallet
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={classnames({
                active: active === '4'
              })}
              onClick={() => {
                toggle('4')
              }}
            >
              NetBanking
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={classnames({
                active: active === '5'
              })}
              onClick={() => {
                toggle('5')
              }}
            >
              zUpcoming
            </NavLink>
          </NavItem>
        </Nav>
        <TabContent activeTab={active}>
          <TabPane style={{ height: '250px' }} tabId="0">
            <SavedCard disablePayButton={setDisablePayButton} />
          </TabPane>
          <TabPane style={{ height: '250px' }} tabId="1">
            <Card disablePayButton={setDisablePayButton} />
          </TabPane>
          <TabPane style={{ height: '250px' }} tabId="2">
            <UPI disablePayButton={setDisablePayButton} />
          </TabPane>
          <TabPane style={{ height: '250px' }} tabId="3">
            <Wallet disablePayButton={setDisablePayButton} />
          </TabPane>
          <TabPane style={{ height: '250px' }} tabId="4">
            <NetBanking disablePayButton={setDisablePayButton} />
          </TabPane>
          <TabPane style={{ height: '250px' }} tabId="5">
            <NetBanking disablePayButton={setDisablePayButton} />
          </TabPane>
        </TabContent>
      </div>
    </div>
  )
}
export default TabsVerticalLeft
