import React, { useState } from 'react'
import { Button, Input } from 'reactstrap'
const UPI = (props) => {
  const [UPIid, setUPIid] = useState('')
  return (
    <div className="card">
      <div className="card-title">Complete UPI Payment within 10 minutes</div>{' '}
      <div className="container" style={{ display: 'flex', marginTop: '10px' }}>
        <Input
          type="text"
          style={{ width: '45%' }}
          value={UPIid}
          placeholder="Enter Your UPI id"
          onChange={(e) => {
            //    console.log('upi', e.target.value)
            setUPIid(e.target.value)
          }}
        />
        <Button.Ripple
          className="ml-1"
          color="primary"
          onClick={(e) => {
            console.log('dzfxgchj')
          }}
        >
          Verify
        </Button.Ripple>
      </div>
    </div>
  )
}
export default UPI
