import React, { useState, Card, CardBody, CardHeader } from 'react'
import Select from 'react-select'
const contactUsOptions = [
  {
    value: 'State Bank of India',
    label: 'State Bank of India',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'HDFC',
    label: 'HDFC',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'PNB',
    label: 'PNB',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'UCO',
    label: 'UCO',
    color: '#00B8D9',
    isFixed: true
  }
]
const NetBanking = (props) => {
  const [banking, setBanking] = useState('')
  return (
    <div className="card">
      <div className="card-title">Select your Bank for Netbanking</div>
      <div className="container" style={{ marginTop: '10px' }}>
        <Select
          id="optionSelect"
          classNamePrefix="select"
          name="color"
          options={contactUsOptions}
          isClearable={true}
          placeholder="Select..."
          onChange={(options) => {
            options?.value && setBanking(options.value)
          }}
        />
      </div>
    </div>
  )
}
export default NetBanking
