import React from 'react'
import { Col, Row, CardHeader, CardBody, CardTitle } from 'reactstrap'

const Summary = ({ setplan, plan }) => {
  return (
    <div
      className={
        window.screen.width <= 500
          ? 'd-block justify-content-around mt-2 col'
          : 'd-flex justify-content-around mt-2 row'
      }
    >
      <div
        className={window.screen.width <= 500 ? 'row' : 'col'}
        style={{
          border: plan === 399 ? '3px solid #ff9f43' : '3px solid #ffffff',
          cursor: 'pointer ',
          borderRadius: '15px',
          padding: '10px',
          height: '290px',
          maxWidth: '350px'
        }}
        onClick={() => {
          setplan(399)
        }}
      >
        <CardHeader
          style={{
            justifyContent: 'center',
            background: plan === 399 ? '#ff9f43' : '#b8c2cc',
            padding: '3px',
            borderRadius: '10px'
          }}
        >
          <CardTitle>Basic Plan</CardTitle>
        </CardHeader>
        <CardBody>
          <div className="detail">
            <div className="detail-amt discount-amt">
              <div>
                Assets <strong>10</strong>
              </div>

              <div>
                Liabilities <strong>10</strong>
              </div>

              <div>
                Secret Diary <strong>10</strong>
              </div>

              <div>
                Daily Spends <strong>10</strong>
              </div>

              <div>
                Password Vault <strong>10</strong>
              </div>

              <div>
                Document Locker <strong>10</strong>
              </div>

              <div>
                Declare Nominees <strong>10</strong>
              </div>
            </div>
          </div>
          <hr />
        </CardBody>
      </div>
      <div
        className={window.screen.width <= 500 ? 'mt-2 row' : 'col'}
        style={{
          border: plan === 999 ? '3px solid #ff9f43' : '3px solid #ffffff',
          cursor: 'pointer ',
          borderRadius: '15px',
          padding: '10px',
          height: '290px',
          maxWidth: '350px'
        }}
        onClick={() => {
          setplan(999)
        }}
      >
        <CardHeader
          style={{
            justifyContent: 'center',
            background: plan === 999 ? '#ff9f43' : '#b8c2cc',
            padding: '3px',
            borderRadius: '10px'
          }}
        >
          <CardTitle>Premium Plan</CardTitle>
        </CardHeader>
        <CardBody>
          <div className="detail">
            <div className="detail-amt discount-amt">
              <div>
                Assets <strong>Unlimited</strong>
              </div>

              <div>
                Liabilities <strong>Unlimited</strong>
              </div>

              <div>
                Secret Diary <strong>Unlimited</strong>
              </div>

              <div>
                Daily Spends <strong>Unlimited</strong>
              </div>

              <div>
                Password Vault <strong>Unlimited</strong>
              </div>

              <div>
                Document Locker <strong>Unlimited</strong>
              </div>

              <div>
                Declare Nominees <strong>Unlimited</strong>
              </div>

              <div>Record Audio/Video messages</div>

              <div>Get legal help for will generation</div>
            </div>
          </div>
          <hr />
        </CardBody>
      </div>
      <div
        className={window.screen.width <= 500 ? 'mt-2 row' : 'col'}
        style={{
          border: plan === 699 ? '3px solid #ff9f43' : '3px solid #ffffff',
          cursor: 'pointer ',
          borderRadius: '15px',
          padding: '10px',
          height: '290px',
          maxWidth: '350px'
        }}
        onClick={() => {
          setplan(699)
        }}
      >
        <CardHeader
          style={{
            justifyContent: 'center',
            background: plan === 699 ? '#ff9f43' : '#b8c2cc',
            padding: '3px',
            borderRadius: '10px'
          }}
        >
          <CardTitle>Advanced Plan</CardTitle>
        </CardHeader>
        <CardBody>
          <div className="detail">
            <div className="detail-amt discount-amt">
              <div>
                Assets <strong>50</strong>
              </div>

              <div>
                Liabilities <strong>50</strong>
              </div>

              <div>
                Secret Diary <strong>50</strong>
              </div>

              <div>
                Daily Spends <strong>50</strong>
              </div>

              <div>
                Password Vault <strong>50</strong>
              </div>

              <div>
                Document Locker <strong>50</strong>
              </div>

              <div>
                Declare Nominees <strong>50</strong>
              </div>

              <div>
                Record Audio/Video messages <strong>50</strong>
              </div>
            </div>
          </div>
          <hr />
        </CardBody>
      </div>
    </div>
  )
}
export default Summary
